export default {
	data() {
		return {
			source: 0,
			addressList: [],
			type_add:'add'
		}
	},
	onLoad(option){
		this.source = option.source;
		this._load()
	},
	onShow() {
		this._load()
	},
	methods: {
		
		_load(){
			this.$api.http.get('address/get_all_address').then(res=>{
				this.addressList = res.data
			})
		},
		//设置默认地址
		set_default(id){
			this.$api.http.post('address/set_default_address',{id:id}).then(res=>{
				this.$api.msg('成功更改默认地址')
				this._load()
			})
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item; 
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.setStorageSync('edit_data',item)
			uni.navigateTo({
				url: `/pages/address/addressManage?type=`+type
			})
		},
		addAddressA(){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=`+this.type_add
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			console.log(data, type);
		}
	},
	onPullDownRefresh() {
		this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
